.button{font-size:1.1rem;font-weight:500;color:#fff;display:flex;flex-direction:row;justify-content:center;align-items:center;cursor:pointer;padding:16px 32px;border-radius:15px;border:none;text-align:center;outline:none;transition:all .3s ease}.button--primary{background:linear-gradient(187deg, #009b77 0%, #003529 85.71%)}@media(hover: hover){.button--primary:hover{filter:brightness(0.9)}}.button--secondary{background:linear-gradient(155deg, #14315f 29.47%, #2966c5 100%)}@media(hover: hover){.button--secondary:hover{filter:brightness(0.9)}}.button--blue{background:linear-gradient(215deg, #009b77 17.78%, #14315f 88.77%)}@media(hover: hover){.button--blue:hover{filter:brightness(0.9)}}.button--gold{background:linear-gradient(157deg, #009b77 14.49%, #6ba557 64.9%, #d4af37 88.77%)}@media(hover: hover){.button--gold:hover{filter:brightness(0.9)}}.button--green{background:linear-gradient(185deg, #009b77 0%, #205449 100%)}@media(hover: hover){.button--green:hover{filter:brightness(0.9)}}.button--accent{background:#009b77;border:3px solid #009b77}@media(hover: hover){.button--accent:hover{background:rgba(0,0,0,0);color:#009b77}}.button--main{background:#14315f;border:3px solid #14315f}@media(hover: hover){.button--main:hover{background:rgba(0,0,0,0);color:#14315f !important}}.button--border-accent{border:3px solid #009b77}@media(hover: hover){.button--border-accent:hover{background:#009b77;color:#fff !important}}.button--border-main{border:3px solid #14315f;color:#000}@media(hover: hover){.button--border-main:hover{background:#14315f;color:#fff}}.button:disabled{background:#7b99b6;color:hsla(0,0%,100%,.7);cursor:not-allowed;border-color:rgba(0,0,0,0)}@media(hover: hover){.button:disabled:hover{background:#7b99b6;color:hsla(0,0%,100%,.7);border-color:rgba(0,0,0,0)}}.button-with-arrow{display:inline-flex;align-items:center;gap:8px}.button-with-arrow::after{content:"→";display:inline-block;margin-left:4px;transition:transform .3s ease}@media(hover: hover){.button-with-arrow:hover ::after{transform:translateX(6px)}}