.introductory-text{max-width:800px;margin:0 auto;padding:32px 24px;background-color:#fff;border-radius:15px;box-shadow:0 4px 6px rgba(0,0,0,.1)}@media screen and (max-width: 768px){.introductory-text{padding:24px 16px}}@media screen and (max-width: 450px){.introductory-text{padding:16px}}.introductory-text__title{color:#14315f;font-size:3rem;font-weight:700;text-align:center;margin-bottom:16px}@media screen and (max-width: 450px){.introductory-text__title{font-size:2.25rem}}.introductory-text__subtitle{color:#009b77;font-size:2.25rem;font-weight:500;text-align:center;margin-bottom:32px}@media screen and (max-width: 450px){.introductory-text__subtitle{font-size:1.5rem;margin-bottom:24px}}.introductory-text__section{margin-bottom:32px}@media screen and (max-width: 450px){.introductory-text__section{margin-bottom:24px}}.introductory-text__section-title{display:flex;align-items:center;font-size:1.5rem;color:#14315f;font-weight:700;margin-bottom:16px;padding-bottom:8px;border-bottom:2px solid #009b77}@media screen and (max-width: 450px){.introductory-text__section-title{font-size:1.3rem}}.introductory-text__icon{margin-right:8px;font-size:1.2em}.introductory-text__paragraph{margin-bottom:16px;line-height:1.5}.introductory-text__list{list-style-type:none;padding-left:24px;margin-bottom:24px}.introductory-text__list li{position:relative;margin-bottom:8px;padding-left:16px}.introductory-text__list li:before{content:"•";color:#009b77;position:absolute;left:0;font-weight:700}.introductory-text__list--documents li{margin-bottom:16px}.introductory-text__list--documents li:before{content:"📄"}.introductory-text__document-link{color:#14315f;text-decoration:none;font-weight:500;transition:all .3s ease-in-out}@media(hover: hover){.introductory-text__document-link:hover{color:#009b77;text-decoration:underline}}.introductory-text__footer{margin-top:32px;text-align:center}.introductory-text__wish{font-size:1.3rem;font-weight:700;color:#009b77}