.open-doors-page{width:100%;max-width:1200px;margin:0 auto;padding:32px 0}.open-doors{display:grid;grid-template-columns:350px 1fr;gap:40px;margin-top:32px;margin-bottom:40px}@media screen and (max-width: 990px){.open-doors{grid-template-columns:300px 1fr;gap:32px}}@media screen and (max-width: 768px){.open-doors{grid-template-columns:1fr;gap:24px}}.open-doors__aside{height:100%;padding-left:24px}@media screen and (max-width: 768px){.open-doors__aside{padding-left:0;width:100%}}.open-doors__sticky-container{position:sticky;top:32px}@media screen and (max-width: 768px){.open-doors__sticky-container{position:static}}.open-doors__link-container{margin-bottom:24px}@media screen and (max-width: 768px){.open-doors__link-container{margin-bottom:16px}}.open-doors__link{color:#009b77;font-weight:500;text-decoration:none;transition:all .3s ease-in-out;font-size:1.125rem}.open-doors__link::before{content:"←";display:inline-block;transition:all .3s ease-in-out;margin-right:8px}@media(hover: hover){.open-doors__link:hover:hover{color:rgb(0,104,79.8451612903)}.open-doors__link:hover:hover::before{transform:translateX(-3px)}}@media screen and (max-width: 450px){.open-doors__link{font-size:.875rem}}.open-doors__event-card{background-color:#fff;border-radius:15px;box-shadow:0 4px 6px rgba(0,0,0,.1);padding:24px;display:flex;flex-direction:column;gap:24px}@media screen and (max-width: 768px){.open-doors__event-card{padding:16px;gap:16px}}.open-doors__date{display:flex;flex-direction:column;align-items:center;padding-bottom:16px;border-bottom:2px solid rgba(0,155,119,.2)}.open-doors__date-number{font-size:3.5rem;font-weight:700;color:#009b77;line-height:1}@media screen and (max-width: 768px){.open-doors__date-number{font-size:2.5rem}}.open-doors__date-month{font-size:1.3rem;color:#33363a}@media screen and (max-width: 768px){.open-doors__date-month{font-size:1.1rem}}.open-doors__details{display:flex;flex-direction:column;gap:16px}.open-doors__detail-item{display:flex;align-items:center;gap:16px;color:#33363a;font-size:1.1rem}@media screen and (max-width: 450px){.open-doors__detail-item{font-size:.875rem}}.open-doors__detail-icon{color:#009b77;font-size:1.2em;min-width:20px;text-align:center}.open-doors__button{width:100%;padding:16px;font-weight:500;text-align:center;transition:all .3s ease-in-out;margin-top:16px}@media(hover: hover){.open-doors__button:hover:hover{transform:translateY(-3px);box-shadow:0 10px 15px rgba(0,0,0,.1)}}.open-doors__counter{display:grid;grid-template-columns:1fr 1fr;gap:16px;margin-top:16px}@media screen and (max-width: 450px){.open-doors__counter{grid-template-columns:1fr}}.open-doors__counter-item{display:flex;flex-direction:column;align-items:center;padding:16px;background-color:rgba(20,49,95,.05);border-radius:10px;transition:all .3s ease-in-out}@media(hover: hover){.open-doors__counter-item:hover:hover{background-color:rgba(20,49,95,.1)}}.open-doors__counter-number{font-size:1.5rem;font-weight:700;color:#14315f}.open-doors__counter-label{font-size:.875rem;color:#33363a;text-align:center}.open-doors__content{background-color:#fff;border-radius:15px;box-shadow:0 4px 6px rgba(0,0,0,.1);padding:32px}@media screen and (max-width: 768px){.open-doors__content{padding:24px}}@media screen and (max-width: 450px){.open-doors__content{padding:16px}}.open-doors__cta{margin-top:40px;padding:32px;background:linear-gradient(187deg, #009b77 0%, #003529 85.71%);border-radius:15px;color:#fff;text-align:center}@media screen and (max-width: 768px){.open-doors__cta{padding:24px;margin-top:32px}}.open-doors__cta-title{font-size:1.5rem;font-weight:700;margin-bottom:24px}@media screen and (max-width: 450px){.open-doors__cta-title{font-size:1.3rem;margin-bottom:16px}}.open-doors__cta-text{font-size:1.1rem;line-height:1.5;margin-bottom:24px;max-width:600px;margin-left:auto;margin-right:auto}@media screen and (max-width: 450px){.open-doors__cta-text{font-size:.875rem}}.open-doors__cta-button{padding:16px 32px;background-color:#fff;color:#fff;font-weight:700;border:none;border-radius:10px;cursor:pointer;transition:all .3s ease-in-out}@media(hover: hover){.open-doors__cta-button:hover:hover{transform:translateY(-3px);box-shadow:0 10px 15px rgba(0,0,0,.1);background-color:hsl(0,0%,95%)}}@media screen and (max-width: 450px){.open-doors__cta-button{padding:8px 24px}}.contentDoors__title{color:#14315f;font-size:2.25rem;font-weight:700;margin-bottom:24px}.contentDoors__title-h2{font-size:1.5rem;font-weight:600;color:#14315f;margin-top:32px;margin-bottom:16px}@media screen and (max-width: 450px){.contentDoors__title-h2{font-size:1.3rem}}.contentDoors__description{font-size:1.1rem;line-height:1.5;color:#33363a;margin-bottom:24px}@media screen and (max-width: 450px){.contentDoors__description{font-size:.875rem}}.contentDoors__img{max-width:100%;height:auto;border-radius:10px;margin:24px 0;box-shadow:0 1px 3px rgba(0,0,0,.12)}.contentDoors__text{font-size:1.1rem;line-height:1.5;color:#33363a}@media screen and (max-width: 450px){.contentDoors__text{font-size:.875rem}}.contentDoors__experts{margin-top:32px}.contentDoors__subtitle{font-size:1.3rem;font-weight:600;color:#14315f;margin-bottom:16px}@media screen and (max-width: 450px){.contentDoors__subtitle{font-size:1.125rem}}.contentDoors__list{padding-left:24px;margin-bottom:24px}.contentDoors__list li{margin-bottom:8px}.contentDoors__list li::marker{color:#009b77;font-size:1.3rem}.success-message{text-align:center;padding:24px}.success-message p{font-size:1.1rem;font-weight:400;color:#33363a;margin-bottom:24px;line-height:1.5}.error-message{font-size:.875rem;font-weight:500;color:#dc3545;padding:16px;border-radius:5px;background-color:rgba(220,53,69,.1);margin-top:16px}