.container-distance{width:100%;padding:24px 0;box-sizing:border-box}.distance-content{max-width:800px;margin-inline:auto;padding:32px 24px;background-color:#fff;border-radius:15px;box-shadow:0 4px 6px rgba(0,0,0,.1);margin-top:32px}@media screen and (max-width: 768px){.distance-content{padding:24px 16px}}@media screen and (max-width: 450px){.distance-content{padding:16px;margin-top:24px}}.distance-text__description{font-size:1.3rem;font-weight:500;color:#14315f;text-align:center;margin-bottom:32px;border-bottom:1px solid #009b77;padding-bottom:16px}@media screen and (max-width: 450px){.distance-text__description{font-size:400;margin-bottom:24px}}.distance-specialties{margin-bottom:40px}.distance-specialties__list{list-style-type:none;padding:0;margin:0}.distance-specialties__item{margin-bottom:16px;padding-left:24px;position:relative}.distance-specialties__item:before{content:"📚";position:absolute;left:0;top:0}@media screen and (max-width: 450px){.distance-specialties__item{padding-left:24px;margin-bottom:8px}}.distance-specialties__link{color:#000;font-weight:500;text-decoration:none;transition:all .3s ease-in-out;font-size:1.1rem}@media(hover: hover){.distance-specialties__link:hover{color:#009b77;text-decoration:underline}}@media screen and (max-width: 450px){.distance-specialties__link{font-size:.875rem}}.distance-additional{display:grid;grid-template-columns:1fr 1fr;gap:32px;margin-top:40px;border-top:1px solid #009b77;padding-top:24px}@media screen and (max-width: 768px){.distance-additional{grid-template-columns:1fr;gap:24px}}.distance-additional__section{display:flex;flex-direction:column;align-items:center;text-align:center;padding:24px;border:1px solid #b0c4de;border-radius:10px;transition:all .3s ease-in-out}@media(hover: hover){.distance-additional__section:hover{transform:translateY(-5px);box-shadow:0 1px 3px rgba(0,0,0,.12)}}.distance-additional__title{color:#14315f;font-size:1.5rem;margin-bottom:16px;font-weight:700}@media screen and (max-width: 450px){.distance-additional__title{font-size:1.3rem}}.distance-additional__link{display:inline-block;background-color:#009b77;color:#fff;text-decoration:none;padding:16px 24px;border-radius:5px;transition:all .3s ease-in-out;font-weight:500}@media(hover: hover){.distance-additional__link:hover{background-color:rgb(0,104,79.8451612903);text-decoration:none}}